@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap');
*{
    margin: 0;
    padding: 0;
    color: rgb(0, 0, 0);
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}
div{
    margin : 0;
    padding: 0;
}
.heading{
    font-family: sans-serif;
    font-size: 30px;
    padding: 10px;
}

.navbar{
    width: 100%;
    background-color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
}
/* s */

.nav-list{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 5px;
}
.nav-list li{
    color: rgb(255, 177, 60);
    font-size: 20px;
    font-weight: 530;
    margin: 10px;
    border: 1px hidden;
    list-style: none;
    transition: all 3s  ease-in-out;
    text-decoration: none;
}
.nav-list>li:hover{
    cursor: pointer;
}
.logo-image{
    
    margin: 0;
    padding: 0;
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.leftnavbar{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
}
.search-input{
    padding: 2px;
    border-radius: 5px;
}
.search-button{
    background-color: rgb(0, 0, 0);
    color: rgb(255, 177, 60);
    padding: 2px;
    margin: 10px;
    border: 2px solid;
    border-radius: 5px;
}
.restro-list{
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding: 0px 0px;
}
.restaurant{
    margin: 10px;
    width: 300px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
}
.restaurant-list
{
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
}
.restaurant>div{
    max-width: 100%;
}
.restaurant-image{
    max-height: 250px;
    max-width: 100%;
    border-radius: 8px;
}
.restro-description{
    
    /* background-color: rgb(118, 189, 165); */
    width: inherit;
    display: flex;
    flex-direction: column; 
    justify-content: center; 
    align-items: flex-start;
    flex-wrap: wrap; 
}
.restro-description>div:first-child{
    color: rgb(37, 22, 2);
}
.cusisines>p
{
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: break-word;
    color: rgb(109, 109, 109);
}
.rating{
    color: rgb(109, 109, 109);
}
.Link{
   list-style: none;
   color: rgb(255, 177, 60);
   text-decoration: none;
}
.shimmer{
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
}
.shimmer-div{
    margin: 10px;
    height: 200px;
    width: 300px;
    background-color: lightgray;
    border-radius: 10px;
    /* display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap; */
}
.shimmer-div>div{
    border-radius: 10px;
    height: 70%;
    width: 100%;
    background-color: lightgray;
}
@media(max-width: 610px){
    .nav-list{
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: 100vh;
        justify-content: center;
        position: fixed;
        top: 0;
        z-index: 100;
        transition: all 0.2s ease-in-out;
        display: none;
    }
}